@charset "UTF-8";

@globalPadding: 30px;
@mainWidth: 1200px; //内容显示区域宽度
@leftWidth: 210px;  //左边栏宽度
@rightWidth: @mainWidth - @leftWidth;   //右侧内容宽度
@bgColor: #e7e8eb;  //背景颜色
@default: #ffffff;
@primary: #337ab7;
@success: #44b549;
@info: #459ae9;
@warning: #f0ad4e;
@error: #d9534f;
@mainColor: #44b549;

@whiteGray: #f4f5f9;
@lightGray: #e7e7eb;
@gray: #8d8d8d;
@darkGray: #d9dadc;
@black: #000000;
@dark: #222222;

@fontSize: 14px;


html,body,h1,h2,h3,h4,h5,h6,div,dl,dt,dd,ul,ol,li,p,blockquote,pre,hr,figure,table,caption,th,td,form,fieldset,legend,input,button,textarea,menu{ margin:0;padding:0;}
header,footer,section,article,aside,nav,hgroup,address,figure,figcaption,menu,details{display:block;}
table{border-collapse:collapse;border-spacing:0;}
caption,th{text-align:left;font-weight:normal;}
html,body,fieldset,img,iframe,abbr{border:0;-webkit-text-size-adjust: none;}
i,cite,em,var,address,dfn{font-style:normal;}
[hidefocus],summary{outline:0;}
li{list-style:none;}
h1,h2,h3,h4,h5,h6,small{font-size:100%;}
sup,sub{font-size:83%;}
pre,code,kbd,samp{font-family:inherit;}
q:before,q:after{content:none;}
textarea{overflow:auto;resize:none;}
label,summary{cursor:default;}
a,button{cursor:pointer;}
h1,h2,h3,h4,h5,h6,em,strong,b{font-weight:bold;}
del,ins,u,s,a,a:hover{text-decoration:none;}
body,textarea,input,button,select,keygen,legend{ color:@dark;outline:0; font: @fontSize/1.5  "Microsoft YaHei","\9ED1\4F53",Arial,sans-serif; font-weight: normal; -webkit-font-smoothing: antialiased;}
@media screen and (-webkit-min-device-pixel-ratio:0){
    body,textarea,input,button,select,keygen,legend{ font: @fontSize/1.5  "Helvetica Neue","Hiragino Sans GB", "Microsoft YaHei","\9ED1\4F53",Arial,sans-serif;}
}

a{
    color:@info;
    &:hover{
        text-decoration: underline;
    }
}

.transparent {
    filter: alpha(opacity=60);
    -khtml-opacity: 0.6;
    -moz-opacity: 0.6;
    opacity: 0.6;
}




/* let's rock */
body{ background: @bgColor;}
.header{ width: 100%; height: 60px; background-color: @default; border-top: 4px solid @mainColor; border-bottom: 1px solid #d9dadc;}
.header-wrap{ width: @mainWidth;}
.logo{ width: auto; height: 100%; vertical-align: middle; line-height: 60px;
    a{
        font-size: @fontSize + 2;
    }
    img{
        height: auto!important;
        height: 100%;
        max-height: 100%;
        vertical-align: middle;
    }
}

.user-info{ width: auto; height: 40px; padding-top: 10px;
    .user-avatar{
        .wh;
        img{
            .wh;
        }
    }
    .user-name{ .wh(auto); line-height: 20px; vertical-align: top; padding-left: 10px;
        p{ display: table-cell; vertical-align: middle;}
    }
}

.user-action{ .wh(auto); line-height: 40px; padding-left: 10px;}

.bd{ width: @mainWidth; height: auto; padding: 36px 0 88px; display: table;}
.container{ width: @mainWidth; .minHeight(700px); border: 1px solid @darkGray; background-color: @default; }

.sidebar{
    width: @leftWidth - 1;
    display: table-cell;
    .minHeight;
    .f-br;
}
.menu-box{ width: 100%; height: auto; padding-bottom: 150px;}
.menu{ padding: 6px 0; .f-bt}
.menu.no-extra{ border-top: 0; margin-top: 13px;}
.menu-title{ padding: 0 30px; width: auto; color: @gray; line-height: 34px;}
.menu-title i{ padding-right: 10px; color: #b2b2b2;}
.menu-item{ width: 100%; line-height: 34px;
    a{
        display: block; padding-left: 58px; color: @dark; text-decoration: none;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-wrap: normal;
        &:hover{
            background-color: @whiteGray;
        }
    }
}
.menu-item.selected{ background: @mainColor;
    a{
        color: @default;
        &:hover{
            background: @mainColor;
        }
    }
}


.footer{ padding: 24px 0; background-color: #b8b9b9; color: #f1f1f1;
    .footer-link{ width: @mainWidth; color: #f1f1f1; font-size: @fontSize - 2;
        li{ display: inline-block; color: #f1f1f1; line-height: 1em; border-right: 1px solid #f1f1f1; padding: 0 1em;
            a{ color: #f1f1f1; font-size: @fontSize - 2;}
        }
        p{ display: inline-block; padding: 0 1em; font-size: @fontSize - 2;}
    }
}


.main{ display: table-cell; width: @rightWidth; height: 200px; vertical-align: top;}
.main-bd{ padding: 20px @globalPadding;}
.main-block{ padding: 15px 0;}
/* 页面标题 */
.page-title{ padding: 0 @globalPadding; line-height: 45px; padding-top: 15px; font-size: @fontSize + 2;
    h2{ font-weight: 400; font-style: normal;}
}
/* 面包屑导航 */
.bread{ padding: 0 @globalPadding; line-height: 40px; .f-bb; color: @dark;
    span{ color: @gray; padding: 0 3px;}
    a{ color: @gray;
        &:hover{ color: @info;}
    }
}
/* tab链接 */
.link-tabs{
    width: 100%; height: 40px; line-height: 40px; .f-bb;
    .link-tab{ float: left;
        a{ display: block; height: 41px; text-align: center; width: auto; padding: 0 30px;  text-decoration: none; color: @dark;
            &:hover{ height: 38px; border-bottom: 3px solid #a0a3ab;}
        }
    }
    .link-tab.selected a{ height: 38px; border-bottom: 3px solid @mainColor;}
}

/* tab-group */
.tab-group{ width: 100%; height: auto;}
.tab-wrap{ width: 100%; height: 40px; line-height: 40px; .f-bb; cursor: pointer;
    .tab{ float: left; height: 41px; text-align: center; width: auto; padding: 0 30px;  text-decoration: none; color: @dark;
            &:hover{ height: 38px; border-bottom: 3px solid #a0a3ab;}
    }
    .tab.selected{ height: 38px; border-bottom: 3px solid @mainColor;}
}
.tab-body{ width: 100%; height: auto;
    .tab-content{ width: 100%; height: auto; display: none; overflow: hidden;}
    .tab-content.active{ display: block;}
}

/* 提示信息区域 */
.info-area{ padding: 9px 30px; background-color: @whiteGray;
    p{ color: @gray; line-height: 1.6;}
}
/* 小标题 */
.sub-title{ width: 100%; line-height: 1em; padding-left: 5px; border-left: 4px solid #6b6b6b; margin: 20px 0; vertical-align: top;
        h2{ display: inline-block; vertical-align: top;}
    }
.sub-title.sub-title-default{ border-color: @default;}
.sub-title.sub-title-primary{ border-color: @primary;}
.sub-title.sub-title-info{ border-color: @info;}
.sub-title.sub-title-success{ border-color: @success;}
.sub-title.sub-title-warning{ border-color: @warning;}
.sub-title.sub-title-error{ border-color: @error;}

/* 表格 */
.table-wrap{ width: 100%;}
.table{ width: 100%; border-top:1px solid @lightGray; .f-bb; border-spacing: 0;
    th, td{ .f-bb; padding: 6px 20px;line-height: 25px;}
    th{ background-color: @whiteGray;}
    td span.divider{ color: @gray;}
    td a{
        color: @dark;
        &:hover{ color: @info;}
    }
    td.td-empty{ padding: 30px 0; color: @gray; font-size: @fontSize + 2;}
}
.table.table-border{ border: 1px solid @lightGray;}
.table.table-toe{ table-layout: fixed; }
.table.table-toe td{ overflow:hidden;text-overflow: ellipsis;white-space:nowrap; }
.table.table-striped tr:nth-child(even){ background: lighten(@whiteGray, 2%);}


/* 按钮 */
.btn{ display: inline-block; text-align: center; .minWidth; height: 30px; line-height: 30px; background-color: @default;  border: 1px solid darken(@default, 10%);border-radius: 3px; padding: 0 12px; cursor: pointer; color: @dark; white-space: nowrap; vertical-align: middle; -webkit-appearance: normal; box-sizing: content-box; background-image: none; margin: 3px 2px;
    &:hover{
        text-decoration: none;
        background-color: darken(@default, 5%)
    }
}

input{ -webkit-appearance: normal}
.btn.btn-default{ background-color: @mainColor; border-color: darken(@mainColor, 7%); color: @default;
    &:hover{
        background-color: darken(@mainColor, 5%);
    }
}
.btn.btn-primary{ background-color: @primary; border-color: darken(@primary, 7%); color: @default;
    &:hover{
        background-color: darken(@primary, 5%);
    }
}
.btn.btn-info{ background-color: @info; border-color: darken(@info, 7%); color: @default;
    &:hover{
        background-color: darken(@info, 5%);
    }
}
.btn.btn-success{ background-color: @success; border-color: darken(@success, 7%); color: @default;
    &:hover{
        background-color: darken(@success, 5%);
    }
}
.btn.btn-warning{ background-color: @warning; border-color: darken(@warning, 7%); color: @default;
    &:hover{
        background-color: darken(@warning, 5%);
    }
}
.btn.btn-error{ background-color: @error; border-color: darken(@error, 7%); color: @default;
    &:hover{
        background-color: darken(@error, 5%);
    }
}

.btn.btn-small{ height: 18px; line-height: 18px;.minWidth(20px;); padding: 0 5px; font-size: @fontSize;}
.btn.btn-short{ .minWidth(40px;); font-size: @fontSize;}

/* 按钮组 */
.btn-group{ display: inline-block; width: auto; overflow: hidden;
    .btn{ border-radius: 0; margin: 0; float: left;  border-radius: 0; overflow: hidden;}
    .btn:not(:first-child){ margin-left: -1px;}
}
.btn-group .btn:first-child{ border-top-left-radius: 3px; border-bottom-left-radius: 3px;}
.btn-group .btn:last-child{ border-top-right-radius: 3px; border-bottom-right-radius: 3px;}

.btn-group.btn-group-vertical{ display: inline-block; width: auto;
    .btn{ border-radius: 0; margin: 0; display: block; width: auto; float: none;}
    .btn:not(:first-child){ margin-left: 0px; margin-top: -1px}
}
.btn-group.btn-group-vertical .btn:first-child{ border-top-left-radius: 3px; border-top-right-radius: 3px;}
.btn-group.btn-group-vertical .btn:last-child{ border-bottom-left-radius: 3px; border-bottom-right-radius: 3px;}


/* 按钮带下拉 */
.btn-group.drop-group{ position: relative; overflow: visible;}
.btn-group.drop-group .btn:nth-last-child(2){ border-top-right-radius: 3px; border-bottom-right-radius: 3px;}
.btn.dropdown-toggle{ .minWidth(0px); padding: 0 8px;}
.drop-menu{ position: absolute; width: auto; padding: 10px 0; border: 1px solid @lightGray; border-radius: 4px; background-color: @default; top: 34px; left: 0; display: none; z-index: 1000;
    li{ display: block; white-space: nowrap; padding: 5px 10px;
        &:hover{ background-color: @whiteGray;}
    }
}
.drop-menu.drop-active{ display: block;}

/* 表单 */
.form-block{ width: 100%; height: auto; padding: 18px 0;
    .form-group{ display: inline-block; padding-right: 20px;}
}
.form-label{ display: inline-block; padding-right: 10px; vertical-align: middle;}
.form-input-box{ display: inline-block; height: 30px; width: auto; position: relative;
    input[type="text"],input[type="password"],input[type="number"]{  border: 1px solid @lightGray; vertical-align: middle; width: 278px; padding: 4px 10px; border-radius: 4px; background-color: transparent; outline: 0; height: 22px; line-height: 22px;}
}

.form-input-box.form-shadow input:focus, .form-textarea-box.form-shadow textarea:focus{border-color: #ccc; border-color: fadein(@info, 60%);
    outline: 0; outline: 0\9;  -webkit-box-shadow: 0 0 8px rgba(82,168,236,.6);
    box-shadow: 0 0 8px fadein(@info, 80%);}

.form-input-box.form-error input, .form-textarea-box.form-error textarea{border-color: #ccc; border-color: fadein(@error, 60%); outline: 0; outline: 0\9;  -webkit-box-shadow: 0 0 6px fadein(@error, 80%); box-shadow: 0 0 6px fadein(@error, 80%);}
.form-input-box.form-success input, .form-textarea-box.form-success textarea{border-color: #ccc; border-color: fadein(@success, 60%); outline: 0; outline: 0\9;  -webkit-box-shadow: 0 0 6px fadein(@success, 80%); box-shadow: 0 0 6px fadein(@success, 80%);}
.form-input-box.form-warning input, .form-textarea-box.form-warning textarea{border-color: #ccc; border-color: fadein(@warning, 60%); outline: 0; outline: 0\9;  -webkit-box-shadow: 0 0 6px fadein(@warning, 80%); box-shadow: 0 0 6px fadein(@warning, 80%);}

.form-input-box.disabled input{ background-color: @whiteGray;}
.form-input-box.size-small input{ width: 128px;}
.form-input-box.size-large input{ width: 578px;}

.form-textarea-box{ display: inline-block; vertical-align: top; width: auto; height: auto; position: relative;}
.form-textarea-box textarea{ float: left; width: 578px; border: 1px solid @lightGray; padding: 8px 10px; border-radius: 4px;}

.form-radio-check-box{ display: inline-block;
    label{ display: inline-block; vertical-align: middle; padding-right: 30px;
        input[type="radio"],input[type="checkbox"]{ vertical-align: middle; margin-right: 5px; margin-top: -2px;}
    }
}
.form-radio-check-box.form-vertical{
    label{ display: block; padding-bottom: 8px; padding-right: 0;}
}

.form-input-date{
    input[type="text"],input[type="password"],input[type="number"]{
        padding-right: 30px; width: 258px;
    }
}
.form-input-date.size-small{
    input[type="text"],input[type="password"],input[type="number"]{
        padding-right: 30px; width: 108px;
    }
}
.form-input-date.size-large{
    input[type="text"],input[type="password"],input[type="number"]{
        padding-right: 30px; width: 558px;
    }
}

.form-select-box{ display: inline-block; width: auto; width: 278px; height: 30px; border: 1px solid @lightGray; border-radius: 4px; padding: 0 10px;
    select{ width: 100%; height: 22px; margin: 4px 0; border: 0; vertical-align: middle;}
}
.form-select-box.size-large{ width: 578px; }
.form-select-box.size-small{ width: 128px; }
.form-select-box.size-auto{ width: auto;}
.form-tips{ display: inline-block; font-size: 12px; padding-left: 5px;}
.form-textarea-box .form-tips{ vertical-align: top;}
.form-tips-down .form-tips{ position: absolute; left: 0; bottom: -22px;}

.form-input-box{ display: inline-block; height: 30px; width: auto; position: relative;
    label.fileupload{ display: inline-block; text-align: center; .minWidth; height: 30px; line-height: 30px; background-color: @default;  border: 1px solid darken(@default, 10%);border-radius: 3px; padding: 0 12px; cursor: pointer; color: @dark; white-space: nowrap; vertical-align: middle; -webkit-appearance: normal; box-sizing: content-box; background-image: none; margin: 3px 2px; position: relative;
        &:hover{
            text-decoration: none;
            background-color: darken(@default, 5%)
        }
        input[type="file"]{ position: absolute; width: 100%; height: 100%; left: 0; top: 0; display: none;}
    }
}

/* 弹窗 */
.dialog-mask{ background-color: #000000; position: absolute; width: 100%; left: 0; top: 0; .transparent;}
.dialog{ background: #ffffff; position: absolute;}
.dialog-head{ padding: 0 20px; background-color: @whiteGray; line-height: 52px; height: 52px; position: relative;
    h2{ font-size: 1.2em; font-weight: 400;}
    .dialog-close{ position: absolute; width:20px; height: 20px; right: 20px; top: 16px; line-height: 20px; text-align: center; font-size: 1.5em; color: @lightGray; font-weight: lighter; cursor: pointer;
        &:hover{ color: @gray;}
    }
}
.dialog-foot{ height: auto; width: 100%; padding: 20px 0; text-align: center; background-color: @whiteGray;}
.dialog-foot .btn{ margin: 0 20px;}
.dialog-body{ padding: 15px 20px;}

/* 分页 */
.page-wrap{ width: 100%; padding: 20px 0;}
.page-con{ display: inline-block;
    .page-nav-area{ display: inline-block;
        a{ display: inline-block; height: 30px; width: 30px; text-align: center; line-height: 30px; border: 1px solid @lightGray; border-radius: 3px; font-size: @fontSize + 4; color: @gray; vertical-align: middle;
            &:hover{ background-color: @whiteGray;}
        }
        .page-num{ display: inline-block; padding: 0 5px; vertical-align: middle;}
    }
    .page-go-area{ display: inline-block; padding-left: 10px;
        input{ width: 65px; height: 22px; padding: 4px 5px; border: 1px solid @lightGray; border-radius: 3px; text-align: center;}
    }
}

/* 徽章 */
.badge{ display: inline-block; min-width: 10px; vertical-align: middle; padding: 4px 7px 3px 5px; font-size: @fontSize - 2; color: #fff; line-height: 1; background: #777; white-space: nowrap; border-radius: 10px; text-align: center; font-weight: 600; margin: 0 4px; position: relative; top: -2px;

}
.badge.badge-default{ background-color: @default; color: @dark;}
.badge.badge-primary{ background-color: @primary;}
.badge.badge-info{ background-color: @info;}
.badge.badge-success{ background-color: @success;}
.badge.badge-warning{ background-color: @warning;}
.badge.badge-error{ background-color: @error;}

/* 代码 */
pre{ padding: 20px 30px 20px 30px; position: relative; margin: 20px 0; background-color: lighten(@lightGray, 5%); border-radius: 4px; overflow: hidden; line-height: 2em;}
.code-type{ position: absolute; top: 0; right: 0; padding: 0 10px; background-color: darken(@lightGray, 5%); border-bottom-left-radius: 4px;}

/* module */
.wh(@width: 40px, @height: 40px){
    width: @width;
    height: @height;
}
.minHeight(@height: 100px){
    height: auto!important; height: @height; min-height: @height;
}
.minWidth(@width: 60px){
    width: auto!important; width: @width; min-width: @width;
}




